.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .popup-content {
    background: #302929;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    font-size: x-large;
    width: 35%;
    height: auto;
  }
  
  .popup-content button {
    margin-top: 10px;
  }
  